.not-found-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-6) 0;
  text-align: center;
}

.not-found-content {
  max-width: 500px;
}

.not-found-page h1 {
  font-size: 8rem;
  font-weight: 700;
  color: var(--primary-color);
  line-height: 1;
  margin-bottom: var(--spacing-3);
}

.not-found-page h2 {
  font-size: 2rem;
  margin-bottom: var(--spacing-3);
}

.not-found-page p {
  font-size: 1.125rem;
  color: var(--neutral-700);
  margin-bottom: var(--spacing-4);
}

.not-found-page .btn {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-2);
}