.auth-layout {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--neutral-200);
  padding: var(--spacing-4);
}

.auth-container {
  display: flex;
  width: 100%;
  max-width: 1200px;
  min-height: 600px;
  background-color: var(--neutral-100);
  border-radius: var(--border-radius-lg);
  overflow: hidden;
  box-shadow: var(--shadow-lg);
}

.auth-sidebar {
  width: 40%;
  background-color: var(--primary-dark);
  color: white;
  padding: var(--spacing-5);
  display: flex;
  flex-direction: column;
}

.auth-brand {
  display: flex;
  align-items: center;
  margin-bottom: var(--spacing-5);
}

.auth-brand h1 {
  margin-left: var(--spacing-3);
  margin-bottom: 0;
  font-size: 1.75rem;
}

.auth-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.auth-info h2 {
  font-size: 2rem;
  margin-bottom: var(--spacing-3);
}

.auth-info p {
  font-size: 1.1rem;
  margin-bottom: var(--spacing-4);
  opacity: 0.9;
}

.auth-features {
  margin-top: var(--spacing-4);
}

.auth-feature {
  display: flex;
  margin-bottom: var(--spacing-4);
}

.auth-feature-icon {
  font-size: 2rem;
  margin-right: var(--spacing-3);
}

.auth-feature-text h3 {
  font-size: 1.1rem;
  margin-bottom: var(--spacing-1);
}

.auth-feature-text p {
  font-size: 0.9rem;
  margin-bottom: 0;
  opacity: 0.8;
}

.auth-content {
  flex: 1;
  padding: var(--spacing-5);
  display: flex;
  flex-direction: column;
}

.auth-nav {
  display: flex;
  justify-content: flex-end;
  margin-bottom: var(--spacing-5);
}

.auth-nav-link {
  margin-left: var(--spacing-3);
  color: var(--neutral-700);
  font-weight: 500;
}

.auth-nav-link:hover {
  color: var(--primary-color);
}

.auth-form-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 400px;
  margin: 0 auto;
  width: 100%;
}

@media (max-width: 900px) {
  .auth-container {
    flex-direction: column;
  }
  
  .auth-sidebar {
    width: 100%;
    padding: var(--spacing-4);
  }
  
  .auth-info h2 {
    font-size: 1.75rem;
  }
  
  .auth-features {
    display: none;
  }
}

@media (max-width: 600px) {
  .auth-layout {
    padding: 0;
  }
  
  .auth-container {
    border-radius: 0;
    min-height: 100vh;
  }
  
  .auth-content {
    padding: var(--spacing-3);
  }
}