/* Hero Section */
.hero-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-5);
  align-items: center;
  padding: var(--spacing-5) 0;
}

.hero-content h1 {
  font-size: 2.75rem;
  line-height: 1.2;
  margin-bottom: var(--spacing-3);
  color: var(--neutral-900);
}

.hero-content p {
  font-size: 1.25rem;
  color: var(--neutral-700);
  margin-bottom: var(--spacing-4);
}

.hero-cta {
  display: flex;
  gap: var(--spacing-3);
}

.hero-cta .btn {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
}

.hero-image {
  border-radius: var(--border-radius-lg);
  overflow: hidden;
  box-shadow: var(--shadow-lg);
}

.hero-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-slow);
}

.hero-image:hover img {
  transform: scale(1.03);
}

/* Features Section */
.features-section {
  padding: var(--spacing-6) 0;
}

.section-header {
  text-align: center;
  margin-bottom: var(--spacing-5);
}

.section-header h2 {
  font-size: 2.25rem;
  color: var(--neutral-900);
  margin-bottom: var(--spacing-2);
}

.section-header p {
  font-size: 1.125rem;
  color: var(--neutral-700);
  max-width: 600px;
  margin: 0 auto;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--spacing-4);
}

.feature-card {
  background-color: white;
  border-radius: var(--border-radius-md);
  padding: var(--spacing-4);
  box-shadow: var(--shadow-sm);
  transition: transform var(--transition-normal), box-shadow var(--transition-normal);
  text-align: center;
}

.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-md);
}

.feature-icon {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: rgba(0, 82, 204, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto var(--spacing-3);
  color: var(--primary-color);
}

.feature-card h3 {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-2);
  color: var(--neutral-900);
}

.feature-card p {
  color: var(--neutral-700);
  line-height: 1.6;
}

/* Featured Internships Section */
.featured-section {
  padding: var(--spacing-6) 0;
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--spacing-4);
}

.view-all-link {
  display: flex;
  align-items: center;
  font-weight: 500;
  color: var(--primary-color);
}

.view-all-link:hover {
  text-decoration: underline;
}

.internships-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--spacing-4);
}

.internship-card {
  background-color: white;
  border-radius: var(--border-radius-md);
  overflow: hidden;
  box-shadow: var(--shadow-sm);
  transition: transform var(--transition-normal), box-shadow var(--transition-normal);
}

.internship-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-md);
}

.internship-card-header {
  position: relative;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--neutral-200);
  padding: var(--spacing-3);
}

.company-logo {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 12px;
  border: 2px solid white;
  box-shadow: var(--shadow-sm);
}

.bookmark-btn {
  position: absolute;
  top: var(--spacing-2);
  right: var(--spacing-2);
  background-color: white;
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--neutral-600);
  box-shadow: var(--shadow-sm);
  transition: color var(--transition-fast), background-color var(--transition-fast);
}

.bookmark-btn:hover {
  color: var(--primary-color);
  background-color: var(--neutral-100);
}

.internship-card-body {
  padding: var(--spacing-3);
}

.internship-card-body h3 {
  font-size: 1.125rem;
  margin-bottom: var(--spacing-1);
  color: var(--neutral-900);
}

.company-name {
  color: var(--neutral-700);
  margin-bottom: var(--spacing-3);
  font-weight: 500;
}

.internship-details {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-2);
}

.detail {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  color: var(--neutral-600);
  font-size: 0.875rem;
}

.internship-card-footer {
  padding: var(--spacing-3);
  border-top: 1px solid var(--neutral-300);
  display: flex;
  justify-content: center;
}

/* Companies Section */
.companies-section {
  padding: var(--spacing-6) 1.5rem;
  background-color: var(--neutral-100);
  border-radius: var(--border-radius-lg);
  margin: var(--spacing-5) 0;
}

.companies-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--spacing-4);
  margin-top: var(--spacing-4);
}

.company-card {
  text-align: center;
  padding: var(--spacing-3);
  background-color: white;
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-sm);
  transition: transform var(--transition-normal);
}

.company-card:hover {
  transform: translateY(-5px);
}

.company-card img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
  margin: 0 auto var(--spacing-3);
}

.company-card h3 {
  font-size: 1.125rem;
  color: var(--neutral-800);
}

/* CTA Section */
.cta-section {
  background-color: var(--primary-dark);
  color: white;
  border-radius: var(--border-radius-lg);
  padding: var(--spacing-6);
  margin: var(--spacing-6) 0;
  text-align: center;
}

.cta-content h2 {
  font-size: 2.25rem;
  margin-bottom: var(--spacing-3);
  color: white;
}

.cta-content p {
  font-size: 1.125rem;
  margin-bottom: var(--spacing-4);
  opacity: 0.9;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta-buttons {
  display: flex;
  gap: var(--spacing-3);
  justify-content: center;
}

.cta-section .btn-outline {
  color: white;
  border-color: white;
}

.cta-section .btn-outline:hover {
  background-color: white;
  color: var(--primary-dark);
}

/* Responsive Styles */
@media (max-width: 1024px) {
  .features-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .companies-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .hero-section {
    grid-template-columns: 1fr;
    text-align: center;
  }
  
  .hero-content h1 {
    font-size: 2.25rem;
  }
  
  .hero-cta {
    justify-content: center;
  }
  
  .internships-grid {
    grid-template-columns: 1fr;
  }
  
  .section-header {
    flex-direction: column;
    gap: var(--spacing-2);
  }
}

@media (max-width: 480px) {
  .features-grid {
    grid-template-columns: 1fr;
  }
  
  .hero-cta {
    flex-direction: column;
  }
  
  .cta-buttons {
    flex-direction: column;
  }
}