/* Page Container */
.internship-details {
  max-width: 1200px;
  margin: auto;
  padding: 2rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

/* Header Section */
.internship-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #eee;
  padding-bottom: 1rem;
  margin-bottom: 2rem;
}

.company-info h1 {
  font-size: 2rem;
  margin: 0;
}

.company-name {
  color: #888;
  font-size: 1.1rem;
  margin-top: 0.5rem;
}

/* Buttons */
.btn {
  padding: 0.6rem 1.2rem;
  border: none;
  cursor: pointer;
  font-weight: 500;
  border-radius: 6px;
  transition: background-color 0.3s ease;
}

.btn-primary {
  background-color: #007BFF;
  color: white;
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.btn-outline {
  background-color: transparent;
  border: 1px solid #ccc;
  color: #333;
}

/* Grid */
.internship-grid {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

.main-content {
  flex: 2;
  min-width: 300px;
}

.sidebar {
  flex: 1;
  min-width: 280px;
}

/* Sections */
section {
  margin-bottom: 2rem;
}

.skills-list,
.perks-list,
.process-steps {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
}

.skill-tag {
  background-color: rgba(0, 123, 255, 0.15); /* light translucent blue */
  color: #007bff; /* Bootstrap blue */
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
}

.perk-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background-color: #e8f5e9;
  padding: 0.5rem 1rem;
  border-radius: 12px;
}

/* Process Steps */
.process-step {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  background-color: #f8f9fa;
  padding: 0.6rem 1rem;
  border-left: 4px solid #007BFF;
  margin-bottom: 0.5rem;
}

.step-number {
  font-weight: bold;
  font-size: 1.2rem;
}

/* Info Cards */
.info-card {
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 1.5rem;
}

.info-card h3 {
  border-bottom: 1px solid #eee;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
}

.info-list,
.date-list {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 0.6rem;
}

/* Modal Overlay */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
  padding: 2rem;
}

/* Modal Box */
.modal-content {
  background: #fff;
  padding: 2rem;
  border-radius: 12px;
  width: 100%;
  max-width: 600px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  max-height: 90vh;
  overflow-y: auto;
}

/* Form Styling */
.modal-content form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.form-group input {
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.btn {
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 6px;
  border: none;
}

.btn-primary {
  background-color: #007bff;
  color: white;
}

.btn-outline {
  background: none;
  border: 1px solid #ccc;
  color: #333;
}
